exports.getPhaseId = function(questions) {
  const scores = questions
    .filter(q => q.stage)
    .reduce((scores, q) => {
      if (scores[q.stage]) {
        scores[q.stage] += q.value
      } else {
        scores[q.stage] = q.value
      }
      return scores
    }, {})
  return Object.keys(scores).reduce((a, b) => {
    return scores[a] > scores[b] ? a : b
  })
}

exports.averageResponse = function(responses){
	var numResponses = Object.keys(responses).length
	var averageResponse = responses.initial
	var qs = averageResponse.surveyData.questions

	qs.forEach(x => x.value = x.value || 0)
	Object.values(responses).forEach(r => {
	  if(r === responses.initial) return
	  r.surveyData.questions.forEach((q, i) => {
	    qs[i].value += (q.value || 0)
	  })
	})
	qs.forEach(q => q.value /= numResponses)
	return averageResponse
}

